<template>
    <Card class="my-card" style="width:320px">
        <Row>
            <Col span="6">
            <img class="my-card-logo" :src="cardData.logo">
            </Col>
            <Col span="12">
            <h4>{{cardData.title}}</h4>
            <p>{{cardData.describe}}</p>
            </Col>
            <Col span="6">
            <Icon type="android-hand"></Icon>
            <span>{{cardData.num}}</span>
            </Col>
        </Row>
    </Card>
</template>
<style>
    .my-card {
        cursor: pointer;
        display: inline-block;
    }

    .my-card-logo {
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
</style>
<script>
    export default {
        props: ['cardData']
    }

</script>


