<template>
    <div class='my-area'>
        <div class='area-title'>
            <Icon :type='areaData.icon' class='icon'></Icon>
            <span>{{areaData.title}}</span>
        </div>
        <my-card v-for='item in areaData.cards' :cardData='item' :key='item.title'></my-card>
    </div>
</template>
<style>
    .my-area {
        margin-bottom: 20px;
    }

    .area-title {
        height: 30px;
        font-size: 18px;
        width: 100px;
        background: white;
        border: 1px solid #dddee1;
        margin-bottom: 10px;
    }

    .icon {
        margin-right: 10px;
    }
</style>
<script>
    import MyCard from '../../../components/card.vue'
    export default {
        props: [
            'areaData'
        ],
        components: {
            'my-card': MyCard
        }
    }

</script>


